html,
body,
#root {
  height: 100%;
  margin: 0;
  color: white;
}
$pageWidth: 400px;
$pageHeight: 250px;
main {
  height: 100%;
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .header {
    display: flex;
    justify-content: end;
    font-size: x-large;
    width: $pageWidth;
  }
  .pageListWrap {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: $pageWidth * 2.5;
    height: $pageHeight + 100;
    .pageListBg {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      .pageListShadow {
        box-shadow: 0 0 50px rgba(255, 255, 255, 0.374);
        width: 50%;
        height: $pageHeight;
        transform: scaleY(0.7);
        border-radius: 100px / 10px;
      }
    }
    .pageList {
      background-color: #000000;
      position: relative;
      height: $pageHeight;
      width: $pageWidth;
      .center {
        z-index: 4;
      }
      .side {
        z-index: 3;
      }
      .sideside {
        z-index: 2;
      }
      .pageWrap:hover {
        cursor: pointer;
      }
      .pageWrap:hover .pageBg {
        box-shadow: 0 0 50px rgba(255, 255, 255, 0.8);
      }
      .pageWrap:hover .title {
        top: $pageHeight + 10;
      }
      .pageWrap {
        position: absolute;
        height: $pageHeight;
        top: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        .title {
          position: absolute;
          transition: top 0.5s;
          top: $pageHeight - 50;
          font-size: x-large;
        }
        .page {
          width: $pageWidth;
          height: $pageHeight;
          background-color: rgb(18, 18, 18);
          position: relative;
          box-shadow: 0 1px 4px rgba(255, 255, 255, 0.3),
            0 0 40px rgba(255, 255, 255, 0.1) inset;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 10px;
          overflow: hidden;
          .img {
            object-fit: cover;
            width: 100%;
            height: 100%;
          }
        }
        .pageBg {
          position: absolute;
          box-shadow: 0 0 30px rgba(255, 255, 255, 0.8);
          top: 20px;
          bottom: 20px;
          left: 0;
          right: 0;
          border-radius: 100px / 10px;
          transition: box-shadow 0.3s;
        }
      }
    }
  }
}
